import React,{useState} from 'react'
import Footer from '../components/Footer/Footer'
import NavbarHome from '../components/NavbarHome'
import SidebarAuth from '../components/NavbarHome/SidebarAuth'
import ScrollToTop from '../components/Signin/ScrollToTop'



const AccueilLayout = ({children}) => {

    const [isOpen, setIsOpen] = useState(false)

    const toggle = () =>{
        setIsOpen(!isOpen)
    }

    return (
        <>
          
            <ScrollToTop/>
           <SidebarAuth isOpen={isOpen}  toggle={toggle}/>
            <NavbarHome  toggle={toggle}/>
            {children}
            <Footer/>
            
        </>
    )
}

export default AccueilLayout
