import React, {useState, useContext, useEffect} from 'react'
import {
    Nav,
    NavbarContainer,
    NavLogo,
    MobileIcon,
    NavMenu,
    NavItem,
    NavLink,
    NavBtn,
    NavBtnLink ,
    NavBtnLogout,
    LogoImg
} from './NavbarStyle'
import {FaBars} from 'react-icons/fa'
import {animateScroll as scroll} from 'react-scroll'
import { AuthContext } from '../../firebase/AuthContext';
import app from '../../firebase/base'
import Img from '../../assets/logo/new-logo-jobsgabon-1.png'



const Navbar = ({toggle}) => {


    const [scrollNav, setScrollNav] = useState(false)
    const {currentUser, logout} = useContext(AuthContext);
    const [role, setRole] = useState("");
    const db = app.firestore();
    var user;


    const getRole = async () => {
        if(currentUser){
         await db.collection("utilisateurs")
          .where("id_utilisateur", "==", currentUser.uid)
          .onSnapshot((querySnapshot) => {
              querySnapshot.forEach((doc) => {
              user = doc.data().role_utilisateur;
              });
             setRole(user);
          });
          }else{
              console.log("Aucun utilisateur connecté")
          }
      }


    const changeNav = () => {
        if(window.scrollY >= 80) {
            setScrollNav(true)
        }
        else {
            setScrollNav(false)
        }
    }

    useEffect(()=> {
        window.addEventListener('scroll', changeNav)
        getRole();
    }, [])

    const toggleHome = () => {
        scroll.scrollToTop();
    }

    return (
        <>
            <Nav scrollNav={scrollNav} >
                <NavbarContainer>
                        <NavLogo to="/" onClick={toggleHome} >
                               <LogoImg src={Img} alt="logo JOBSGABON" />
                        </NavLogo>
                        <MobileIcon  onClick={toggle} >
                            <FaBars />
                        </MobileIcon>
                        <NavMenu>
                        <NavItem>
                                <NavLink
                                    to="about"
                                    smooth={true}
                                    duration={500} 
                                    spy={true}
                                    exact={true}
                                    offset={-80}

                                >
                                    À propos
                                </NavLink>
                            </NavItem>
                            <NavItem>
                                <NavLink
                                to="produits"
                                smooth={true}
                                duration={500}
                                spy={true}
                                exact={true}
                                offset={-80}>
                                    Nos produits
                                </NavLink>
                            </NavItem>
                            <NavItem>
                                <NavLink
                                to="tarifs"
                                smooth={true}
                                duration={500}
                                spy={true}
                                exact={true}
                                offset={-80}

                                >Nos Tarifs</NavLink>
                            </NavItem>

                        </NavMenu>
                        <NavBtn>

                        {!currentUser &&
                            (
                                <NavBtnLink to="/recruteur/connexion" scrollNav={scrollNav}>Se connecter</NavBtnLink>
                             )
                         }

                         {!currentUser &&

                                (
                                        <NavBtnLink to="/recruteur/signup" scrollNav={scrollNav}>S'inscrire</NavBtnLink>
                                    )
                         }

                         {currentUser &&
                           (
                                <NavBtnLogout onClick={logout} scrollNav={scrollNav}>Déconnexion</NavBtnLogout>
                            )
                          }

                          {currentUser && role == "recruteur" &&
                            (
                                <NavBtnLink to="/recruteur/dashboard" scrollNav={scrollNav}>Mon compte</NavBtnLink>
                            )
                           }
                        </NavBtn>
                </NavbarContainer>
            </Nav>
        </>
    )
}

export default Navbar
