import React, {useState,useContext, useEffect} from 'react'
import {
    Nav,
    NavbarContainer,
    NavLogo,
    MobileIcon,
    NavMenu,
    NavItem,
    NavLink,
    NavBtn,
    NavBtnLink,
    NavBtnLogout,
    LogoImg,
    NavLinkR

} from './NavbarElements'
import {FaBars} from 'react-icons/fa'
import {animateScroll as scroll} from 'react-scroll'
import { AuthContext } from '../../firebase/AuthContext';
import app from '../../firebase/base'
import Img from '../../assets/logo/new-logo-jobsgabon-2.png'


const NavbarHome = ({toggle}) => {

    const [scrollNav, setScrollNav] = useState(false)
    const {currentUser, logout} = useContext(AuthContext);
    const [role, setRole] = useState("");
    const db = app.firestore();
    var user;


    const getRole = async () => {
        if(currentUser){
         await db.collection("utilisateurs")
          .where("id_utilisateur", "==", currentUser.uid)
          .onSnapshot((querySnapshot) => {
              querySnapshot.forEach((doc) => {
              user = doc.data().role_utilisateur;
              });
             setRole(user);
          });
          }else{
              console.log("Aucun utilisateur connecté")
          }
      }




    const changeNav = () => {
        if(window.scrollY >= 80) {
            setScrollNav(true)
        }
        else {
            setScrollNav(false)
        }
    }

    useEffect(()=> {
        window.addEventListener('scroll', changeNav);
        getRole();
    }, [role])

    const toggleHome = () => {
        scroll.scrollToTop();
    }

    return (
        <>
            <Nav scrollNav={scrollNav} >
                <NavbarContainer>
                        <NavLogo to="/" onClick={toggleHome} scrollNav={scrollNav} >
                            <LogoImg src={Img} alt="logo JOBSGABON" />
                        </NavLogo>
                        <MobileIcon  onClick={toggle} >
                            <FaBars />
                        </MobileIcon>
                        <NavMenu>

                          <NavItem>
                                <NavLinkR scrollNav={scrollNav}
                                to="/candidats"
                                smooth={true}
                                duration={500}
                                spy={true}
                                exact={true}
                                offset={-80}

                                >Candidats</NavLinkR>
                            </NavItem>
                            <NavItem>
                                <NavLinkR scrollNav={scrollNav}
                                to="/recruteur"
                                smooth={true}
                                duration={500}
                                spy={true}
                                exact={true}
                                offset={-80}

                                >Recruteurs</NavLinkR>
                            </NavItem>
                            <NavItem>
                                <NavLinkR scrollNav={scrollNav}
                                to="/candidats/emplois"
                                smooth={true}
                                duration={500}
                                spy={true}
                                exact={true}
                                offset={-80}>
                                   Offres d'emploi
                                </NavLinkR>
                            </NavItem>


                        </NavMenu>
                        <NavBtn>
                        {!currentUser &&
                           (
                            <NavBtnLink scrollNav={scrollNav} to="/connexion" >Se connecter</NavBtnLink>
                            )
                        }

                        {currentUser &&
                         (
                            <NavBtnLogout  onClick={logout} scrollNav={scrollNav} to="/connexion" >Déconnexion</NavBtnLogout>
                            )
                        }

                        {currentUser && role == "admin" &&
                         (
                            <NavBtnLink to="/admin/dashboard" scrollNav={scrollNav}  >Admin</NavBtnLink>
                            )
                        }
                        </NavBtn>
                </NavbarContainer>
            </Nav>
        </>
    )
}

export default NavbarHome
