import React,{useState}  from 'react'
import Footer from '../components/Footer/Footer'
import SecondNavbar from '../components/NavbarHome/SecondNavbar'
import SidebarAuth from '../components/NavbarHome/SidebarAuth'
import ScrollToTop from '../components/Signin/ScrollToTop'



const AccueilLayoutTwo = ({children}) => {

    const [isOpen, setIsOpen] = useState(false)

    const toggle = () =>{
        setIsOpen(!isOpen)
    }

    return (
        <>
          
            <ScrollToTop/>
            <SidebarAuth isOpen={isOpen}  toggle={toggle}/>
            <SecondNavbar  toggle={toggle}/>
            {children}
            <Footer/>
            
        </>
    )
}

export default AccueilLayoutTwo
